import React from 'react';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';
import { PanelHeader, HeaderButton, Alert, Div, Input, Spinner, FormLayout, Group, Cell, Avatar, CellButton, platform, IOS } from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

import connect from '@vkontakte/vkui-connect-promise';
import { getTimeString } from '../utils.js';

const apieva = require('../apieva');
const osname = platform();

class Main_SendMoneys extends React.Component {
	constructor(props) {
		super(props);
        this.state = { target: ' ' };

		this.params = { target: 0, count: 0 };

		this.sendMoneys = apieva.makeAction('profile', 'sendMoneys', this.params, this, (a, b) => {
			//alert(JSON.stringify(b))
			if (!b.error) {
				this.props.openSheet(<Alert
	        		actions={[{ title: '–û–∫', autoclose: true, }]}
	        		onClose={() => { this.props.close(); this.props.openSheet(null); }}
	      		>
	        		<h2>–£—Å–ø–µ—Ö</h2>
	        		<p>–í—ã —É—Å–ø–µ—à–Ω–æ –æ—Ç–ø—Ä–∞–≤–∏–ª–∏ –±–∏—Ç—ã –Ω–∞ —Å—á–µ—Ç {b.nickname}</p>
	      		</Alert>);

				this.props.update();
			}else{
				this.props.openSheet(<Alert
					actions={[{ title: '–ù–∞–∑–∞–¥', autoclose: true, }]}
					onClose={() => { this.props.openSheet(null); }}
				>
					<h2>–û—à–∏–±–∫–∞</h2>
					<p>{b.error}</p>
				</Alert>);
			}
		});

        this.onTargetChange = (e) => {
            const { value } = e.currentTarget;
            if (value == '') return this.setState({ target: ' ' });
            this.setState({ target: null, istarget: false });

            let userScreenName = value;
            if (userScreenName.includes('/')) {
                let arr = userScreenName.split('/');
                userScreenName = arr[arr.length - 1];
            }

            apieva.send('utils', 'getUser', { user_ids: userScreenName, fields: 'photo_200' }).then(r => {
                //alert(r.data.response)
                if (!r.data.error && r.data.response[0]) {
                    let user = r.data.response[0];
					this.params.target = user.id;
					//alert(JSON.stringify(this.params))
                    this.setState({
						istarget: true,
						target: <Cell before={user.photo_200  ? <Avatar src={user.photo_200}/> : <Avatar>üì∑</Avatar>}>{`${user.first_name} ${user.last_name}`}</Cell>
                    });
                }
                else {
                    this.setState({ istarget: false, target: <Cell before={<Avatar>‚ùì</Avatar>}>–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –Ω–µ –Ω–∞–π–¥–µ–Ω</Cell> });
                }
            });
        }
    }

    render() {
        return (
            <Div>
                <PanelHeader left={<HeaderButton onClick={this.props.close}>{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}</HeaderButton>}>
                    üí∏ –û—Ç–ø—Ä–∞–≤–∏—Ç—å –±–∏—Ç—ã
                </PanelHeader>
                <Group title="–ó–∞–ø–æ–ª–Ω–∏—Ç–µ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—é –¥–ª—è –æ—Ç–ø—Ä–∞–≤–∫–∏">
                    <FormLayout>
                        <Input onChange={e => this.params.count = e.currentTarget.value} placeholder="–°–∫–æ–ª—å–∫–æ –æ—Ç–ø—Ä–∞–≤–∏—Ç—å –±–∏—Ç?" />
                        <Input onChange={this.onTargetChange} placeholder="–ö–æ–º—É –æ—Ç–ø—Ä–∞–≤–∏—Ç—å –±–∏—Ç—ã?" />
                    </FormLayout>
                </Group>
                <Group>
                    {this.state.target ? this.state.target : <Cell before={<Avatar><Spinner/></Avatar>}>–ó–∞–≥—Ä—É–∑–∫–∞...</Cell>}
					{this.state.istarget==true ? <CellButton
						onClick={this.sendMoneys}
						size="xl"
					>
						–û—Ç–ø—Ä–∞–≤–∏—Ç—å –±–∏—Ç—ã
					</CellButton> : ''}
                </Group>
            </Div>
    	);
	}
}

Main_SendMoneys.propTypes = {
	userToken: PropTypes.string.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string
	}),
};

export default Main_SendMoneys;
